
'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { Textarea } from '@/components/ui/textarea';
import { Loader2, Download, CheckCircle, XCircle, AlertTriangle, FileText, FileSpreadsheet } from 'lucide-react';
import { format } from 'date-fns';
import { ptBR } from 'date-fns/locale';

interface Employee {
  id: string;
  name: string;
  cpf: string;
  position: string;
  contract?: { name: string; client?: { name: string } };
}

interface DailyDetail {
  date: string;
  entry: string;
  lunchOut: string;
  lunchIn: string;
  exit: string;
  total: string;
  status: string;
  statusColor: string;
}

interface PointMirror {
  id: string;
  startDate: string;
  endDate: string;
  expectedHours: number;
  workedHours: number;
  overtimeHours: number;
  absenceHours: number;
  lateHours: number;
  status: string;
  integrityHash?: string;
  observation?: string;
  employee: Employee;
  contract: { name: string };
  dailyDetails?: DailyDetail[];
}

export default function EspelhoPontoPage() {
  const [employees, setEmployees] = useState<Employee[]>([]);
  const [selectedEmployeeId, setSelectedEmployeeId] = useState<string>('');
  const [selectedMonth, setSelectedMonth] = useState<string>(new Date().toISOString().slice(0, 7)); // YYYY-MM
  const [mirror, setMirror] = useState<PointMirror | null>(null);
  const [loading, setLoading] = useState(false);
  const [generating, setGenerating] = useState(false);
  const [observation, setObservation] = useState('');

  useEffect(() => {
    fetchEmployees();
  }, []);

  useEffect(() => {
    if (mirror) {
        setObservation(mirror.observation || '');
    }
  }, [mirror]);

  const fetchEmployees = async () => {
    try {
      const res = await fetch('http://localhost:4000/colaboradores');
      const data = await res.json();
      setEmployees(data);
    } catch (error) {
      console.error('Failed to fetch employees', error);
    }
  };

  const handleGenerate = async () => {
    if (!selectedEmployeeId || !selectedMonth) return;
    setGenerating(true);
    try {
      // 1. Generate/Update Report
      const genRes = await fetch('http://localhost:4000/reports/generate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          employeeId: selectedEmployeeId,
          date: `${selectedMonth}-01`,
        }),
      });
      const genData = await genRes.json();
      
      // 2. Fetch Full Details
      const detailRes = await fetch(`http://localhost:4000/reports/${genData.id}`);
      const detailData = await detailRes.json();
      
      setMirror(detailData);
    } catch (error) {
      console.error('Failed to generate report', error);
    } finally {
      setGenerating(false);
    }
  };

  const handleApprove = async () => {
    if (!mirror) return;
    if (!confirm('Tem certeza? Após aprovado, o espelho não poderá ser alterado.')) return;

    try {
      // Mock User ID for approval (In real app, get from auth context)
      const userId = 'admin-id'; 
      const res = await fetch(`http://localhost:4000/reports/${mirror.id}/approve`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId, observation }),
      });
      const updatedMirror = await res.json();
      setMirror({ ...mirror, ...updatedMirror }); // Optimistic update or refetch
      handleGenerate(); // Refresh to be safe
      alert('Espelho aprovado com sucesso!');
    } catch (error) {
      alert('Erro ao aprovar espelho');
    }
  };

  const handleReject = async () => {
      if (!mirror) return;
      if (!observation) return alert('Por favor, informe o motivo da reprovação no campo de observações.');

      try {
        const res = await fetch(`http://localhost:4000/reports/${mirror.id}/reject`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ observation }),
        });
        handleGenerate(); // Refresh
        alert('Espelho reprovado.');
      } catch (error) {
          alert('Erro ao reprovar.');
      }
  };

  const handleDownloadPdf = () => {
      if (!mirror) return;
      window.open(`http://localhost:4000/reports/${mirror.id}/pdf`, '_blank');
  };

  const isReadOnly = mirror?.status === 'APROVADO';

  return (
    <div className="p-6 space-y-6 max-w-7xl mx-auto">
      {/* Header Section */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div>
           <h1 className="text-3xl font-bold tracking-tight text-gray-900">Espelho de Ponto</h1>
           <p className="text-gray-500">Gestão e conferência de jornada mensal.</p>
        </div>
        
        <div className="flex flex-col sm:flex-row gap-2 w-full md:w-auto">
          <Select onValueChange={setSelectedEmployeeId} value={selectedEmployeeId}>
            <SelectTrigger className="w-full md:w-[250px]">
              <SelectValue placeholder="Selecione o Colaborador" />
            </SelectTrigger>
            <SelectContent>
              {employees.map((emp) => (
                <SelectItem key={emp.id} value={emp.id}>
                  {emp.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          <input
            type="month"
            className="border rounded px-3 py-2 text-sm"
            value={selectedMonth}
            onChange={(e) => setSelectedMonth(e.target.value)}
          />

          <Button onClick={handleGenerate} disabled={generating}>
            {generating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <FileText className="mr-2 h-4 w-4" />}
            Gerar
          </Button>
        </div>
      </div>

      {mirror && (
        <div className="space-y-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
          
          {/* Header Card & Actions */}
          <div className="flex justify-between items-center">
             <div className="flex items-center gap-2">
                <h2 className="text-xl font-semibold">
                    {format(new Date(mirror.startDate), 'MMMM / yyyy', { locale: ptBR }).toUpperCase()}
                </h2>
                <Badge 
                    className={
                        mirror.status === 'APROVADO' ? 'bg-green-600' : 
                        mirror.status === 'REPROVADO' ? 'bg-red-600' : 'bg-yellow-600'
                    }
                >
                    {mirror.status}
                </Badge>
             </div>
             <div className="flex gap-2">
                 <Button variant="outline" size="sm" onClick={handleDownloadPdf}>
                     <Download className="mr-2 h-4 w-4" /> PDF
                 </Button>
                 <Button variant="outline" size="sm">
                     <FileSpreadsheet className="mr-2 h-4 w-4" /> Excel
                 </Button>
             </div>
          </div>

          {/* Employee Info Card */}
          <Card className="bg-slate-50 border-slate-200">
            <CardContent className="pt-6 grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
              <div className="col-span-2">
                <p className="text-xs font-medium text-gray-500 uppercase">Colaborador</p>
                <p className="font-bold text-lg text-gray-900 truncate">{mirror.employee?.name || 'N/A'}</p>
              </div>
              <div>
                <p className="text-xs font-medium text-gray-500 uppercase">CPF</p>
                <p className="font-medium text-gray-900">{mirror.employee?.cpf || 'N/A'}</p>
              </div>
              <div>
                <p className="text-xs font-medium text-gray-500 uppercase">Cargo</p>
                <p className="font-medium text-gray-900 truncate">{mirror.employee?.position || 'N/A'}</p>
              </div>
              <div>
                <p className="text-xs font-medium text-gray-500 uppercase">Contrato</p>
                <p className="font-medium text-gray-900 truncate">{mirror.contract?.name || 'N/A'}</p>
              </div>
               <div>
                <p className="text-xs font-medium text-gray-500 uppercase">Cliente</p>
                <p className="font-medium text-gray-900 truncate">{mirror.employee?.contract?.client?.name || 'N/A'}</p>
              </div>
            </CardContent>
          </Card>

          {/* Summary Cards */}
          <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
            <Card>
              <CardContent className="pt-4 pb-4">
                <p className="text-xs text-muted-foreground mb-1">Horas Previstas</p>
                <div className="text-2xl font-bold">{Number(mirror.expectedHours).toFixed(2)}</div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-4 pb-4 border-l-4 border-green-500">
                <p className="text-xs text-muted-foreground mb-1">Trabalhadas</p>
                <div className="text-2xl font-bold text-green-700">{Number(mirror.workedHours).toFixed(2)}</div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-4 pb-4 border-l-4 border-orange-500">
                <p className="text-xs text-muted-foreground mb-1">Extras</p>
                <div className="text-2xl font-bold text-orange-700">{Number(mirror.overtimeHours).toFixed(2)}</div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-4 pb-4 border-l-4 border-red-500">
                <p className="text-xs text-muted-foreground mb-1">Faltas</p>
                <div className="text-2xl font-bold text-red-700">{Number(mirror.absenceHours).toFixed(2)}</div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-4 pb-4 border-l-4 border-yellow-500">
                <p className="text-xs text-muted-foreground mb-1">Atrasos</p>
                <div className="text-2xl font-bold text-yellow-700">{Number(mirror.lateHours).toFixed(2)}</div>
              </CardContent>
            </Card>
          </div>

          {/* Detailed Table */}
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-lg">Jornada Detalhada</CardTitle>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow className="bg-gray-50 hover:bg-gray-50">
                    <TableHead className="w-[100px]">Data</TableHead>
                    <TableHead>Entrada</TableHead>
                    <TableHead>Saída Almoço</TableHead>
                    <TableHead>Volta Almoço</TableHead>
                    <TableHead>Saída</TableHead>
                    <TableHead className="font-bold">Total</TableHead>
                    <TableHead>Ocorrência</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {mirror.dailyDetails?.map((day, idx) => (
                      <TableRow key={idx}>
                          <TableCell className="font-medium">{format(new Date(day.date), 'dd/MM')}</TableCell>
                          <TableCell>{day.entry}</TableCell>
                          <TableCell>{day.lunchOut}</TableCell>
                          <TableCell>{day.lunchIn}</TableCell>
                          <TableCell>{day.exit}</TableCell>
                          <TableCell className="font-bold">{day.total}</TableCell>
                          <TableCell>
                              <Badge variant="outline" 
                                style={{ 
                                    color: day.statusColor === 'red' ? '#ef4444' : day.statusColor === 'yellow' ? '#d97706' : day.statusColor === 'green' ? '#16a34a' : '#6b7280',
                                    borderColor: 'currentColor'
                                }}
                              >
                                  {day.status}
                              </Badge>
                          </TableCell>
                      </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>

           {/* Observations & Actions */}
           <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
               <div className="md:col-span-2">
                   <label className="text-sm font-medium mb-2 block">Observações / Ajustes</label>
                   <Textarea 
                        placeholder="Ex: Ajuste realizado conforme escala do contrato..." 
                        value={observation}
                        onChange={(e) => setObservation(e.target.value)}
                        disabled={isReadOnly}
                        className="min-h-[100px]"
                   />
               </div>
               
               <div className="flex flex-col justify-end gap-3">
                   {!isReadOnly && (
                       <>
                        <Button onClick={handleApprove} className="w-full bg-green-600 hover:bg-green-700">
                            <CheckCircle className="mr-2 h-4 w-4" />
                            Aprovar Espelho
                        </Button>
                        <Button onClick={handleReject} variant="destructive" className="w-full">
                            <XCircle className="mr-2 h-4 w-4" />
                            Reprovar com Observação
                        </Button>
                       </>
                   )}
                   {isReadOnly && (
                       <div className="p-4 bg-gray-50 rounded border border-gray-200 text-center">
                           <p className="text-sm text-gray-500 mb-1">Este espelho está aprovado e bloqueado.</p>
                           <p className="text-xs text-gray-400 break-all font-mono">{mirror.integrityHash}</p>
                       </div>
                   )}
               </div>
           </div>
        </div>
      )}
    </div>
  );
}
